import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'

@inject('store') @observer class SrchResults extends Component {

  useStation  = (event) => {
    this.props.userParams.sid = this.props.stationList[event.target.value].id;
    this.props.store.app.stnName = this.props.stationList[event.target.value].name + ' ' + this.props.stationList[event.target.value].vdr;
    this.props.hideStationSearch();
    this.props.onSidChange();
  }

  render() {
    const elemInfo = {maxt: 'Max temp', mint: 'Min temp', pcpn: 'Precip', snow: 'Snow', snwd: 'Snow depth', obst: 'Obs temp'};
    return (
      <div style={{textAlign:'center'}}>
        <span><b>{this.props.results.srchProgress}</b></span>
        <span><b>{this.props.results.srchError}</b></span>
        {this.props.stationList.length === 1 &&
          <div>
            <span><b>Matches:</b></span><br/>
            <span>{this.props.stationList[0].name}</span><br/>
            <button onClick={this.useStation} value="0">Use this station</button>
          </div>
        }
        {this.props.stationList.length > 1 &&
          <div>
            <span><b>Nearby stations reporting {elemInfo[this.props.userParams.selectedElem]}:</b></span><br/>
            <select onChange={this.useStation}>
              <option key="0">Select...</option>
              {this.props.stationList.map((sta, index) => (
                <option key={index} value={index}>{sta.name}</option>
              ))}
            </select>
          </div>
        }
      </div>
    )
  }
}

export default SrchResults



