import React, { Component } from 'react'
import {observer} from 'mobx-react'
import axios from 'axios'
import InputText from './InputText'
import SrchResults from './SrchResults'

const fieldInputs = {
  anyId: {label: 'Other station id:', placeholder: "coop, wban, etc."},
  srchName: {label: 'Search location:', placeholder: "City,state or zip code"}
}

@observer class StationSearch extends Component {
  constructor(props) {
    super(props)
    this.state = {
      srchProgress: "",
      srchError: ""
    }
  }

  srchCenter = null
  
  onChange = (event) => {
    this.setState({srchError: "", srchProgress: ""});
    this.props.srchParams.stationList = [];
    if (event.target.name === "anyId") {
      this.props.srchParams.anyId = event.target.value;
      this.props.srchParams.srchName = "";
    } else {
      this.props.srchParams.srchName = event.target.value;
      this.props.srchParams.anyId = "";
    }
  }
 
  sortByDistance = (a, b) => {
    const x = a.distance, y = b.distance;
    return ((x < y) ? -1 : ((x > y) ? 1 : 0));
  }

  geoDistance = (lat1, lon1, lat2, lon2) => {
    if ((lat1 === lat2) && (lon1 === lon2)) {
      return 0;
    } else {
      const radlat1 = Math.PI * lat1/180;
      const radlat2 = Math.PI * lat2/180;
      const theta = lon1-lon2;
      const radtheta = Math.PI * theta/180;
      let dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
      if (dist > 1) {
        dist = 1;
      }
      dist = Math.acos(dist);
      dist = dist * 180/Math.PI;
      dist = dist * 60 * 1.1515;
      return dist;
    }
  }
  
  getInfoFromMeta = (metaResults) => {
    for (let i = 0; i < metaResults.sids.length; i += 1) {
      const sid = metaResults.sids[i].split(" ");
      let distance = null;
      if (this.srchCenter) {
        distance = this.geoDistance(this.srchCenter.lat, this.srchCenter.lng, metaResults.ll[1], metaResults.ll[0]);
      }
      if (sid[1] === "6") {
        return {
          id: sid[0],
          name: metaResults.name + ', ' + metaResults.state,
          distance: distance
        };
      }
    }
    return null;
  }

  processMetaResults = (res) => {
    let nearbyStns = [];
    for (let j = 0; j < res.data.meta.length; j += 1) {
      const metaObj = this.getInfoFromMeta(res.data.meta[j]);
      if (metaObj) {
        nearbyStns.push(metaObj);
      }
    }
    if (nearbyStns.length) {
      nearbyStns.sort(this.sortByDistance);
      this.setState({srchProgress: ""});
      this.props.srchParams.stationList = nearbyStns;
    } else {
      this.setState({srchError: "No matches", srchProgress: ""});
    }
  }
    
  getMetaData = (addParam) => {
   const cparams = Object.assign({
     elems: this.props.userParams.selectedElem,
     meta: "name,state,sids,valid_daterange,ll"
   }, addParam);
   this.setState({srchProgress: "Searching for stations..."});
   axios
    .get("http://data.rcc-acis.org/StnMeta", { params: {params: cparams }
    })
    .then(res => {
      this.processMetaResults(res);
    })
    .catch(err => {
      console.log("ACIS Request Error: " + (err.response.data || err.response.statusText));
      this.setState({srchError: "Search error", srchProgress: ""});
    });
  }
  
  processGeoResults = (results) => {
    if (results.info.statuscode !== "0") {
      const lat = results.results[0].locations[0].latLng.lat;
      const lon = results.results[0].locations[0].latLng.lng;
      const lat_offset = 25.0 / 69.047;
      const lon_offset = 25.0 / (69.17 * Math.cos(lat * 0.01745));
      const requestBbox = [lon - lon_offset, lat - lat_offset, lon + lon_offset, lat + lat_offset];
      this.srchCenter= results.results[0].locations[0].latLng;
      this.getMetaData({bbox: requestBbox});
    } else {
      this.setState({srchError: "Geo search error "+results.info.statuscode, srchProgress: ""});
    }
  }
  
  doLocationSearch = () => {
    this.setState({srchProgress: "Searching for location..."});
    const url = new URL("https://www.mapquestapi.com/geocoding/v1/address?location=" + this.props.srchParams.srchName + "&inFormat=kvp&outFormat=json&thumbMaps=false&key=RrK7pA4HaZD1m3lTTfxG9wyOXJtwf2e4");
    fetch(url, {
      method: 'POST',
    })
      .then(response => response.json())
      .then(data => this.processGeoResults(data))
      .catch(error => this.setState({srchError: 'Error: '+error.message}))
  }

  doSearch = (e) => {
    e.preventDefault();
    if (this.props.srchParams.anyId.length > 0) {
      this.getMetaData({sids: this.props.srchParams.anyId});
    } else if (this.props.srchParams.srchName.length > 0) {
      this.doLocationSearch();
    } else {
      alert("Must specify an ID or location");
    }
  }

  render() {
    return (
      <div style={{border:'1pt solid gray', display:'inline-block', padding:'6px', margin:'1em 0', background:'#f1f1f1'}}>
        <div style={{display:'inline-block'}}>
          <b>Search by:</b>
          <br />
          <InputText
            name="anyId"
            size="13"
            value={this.props.srchParams.anyId} 
            placeholder={fieldInputs.anyId.placeholder} 
            label={fieldInputs.anyId.label} 
            onChange={this.onChange} 
          />
          <br />- or -<br />    
          <InputText 
            name="srchName" 
            size="20" 
            value={this.props.srchParams.srchName} 
            placeholder={fieldInputs.srchName.placeholder} 
            label={fieldInputs.srchName.label} 
            onChange={this.onChange} 
          />
          <br />
          <button id="ssgo"  onClick={this.doSearch}>Search</button>
        </div>
        <div style={{display:'inline-block', verticalAlign:'top', marginLeft:'2em'}}>
          <SrchResults results={this.state} stationList={this.props.srchParams.stationList} userParams={this.props.userParams} hideStationSearch={this.props.hideStationSearch} onSidChange={this.props.onSidChange} />
        </div>
      </div>
    )
  }
}

export default StationSearch



