import React, { Component } from 'react'
import { observable, computed } from 'mobx'
import UserInput from './UserInput'
import Results from './Results'
import About from './About'

class App extends Component {

  @observable userParams = {
    sid: "",
    selectedElem: "pcpn",
    syr: (new Date()).getFullYear(),
    eyr: (new Date()).getFullYear(),
    cdoUrl: computed(this.buildCdoUrl),
    acisParams: computed(this.buildAcisParams)
  }

  constructor(props) {
    super(props)
    this.state = {showResults: false, showAbout: true}
  }
  
  buildCdoUrl = () => {
    let cdourl = "https://www.ncei.noaa.gov/access/services/data/v1?dataset=daily-summaries";
    const cdoDatatypes = {
      "maxt": "TMAX", "mint": "TMIN", "pcpn": "PRCP,MDPR,DAPR", "snow": "SNOW,MDSF,DASF", "snwd": "SNWD", "obst": "TOBS"
    };
    const data_specs = {
      units: "standard",
      stations: (this.userParams.sid.toUpperCase() || "USC00304174"),
      startDate: this.userParams.syr + "-01-01",
      endDate: this.userParams.eyr+ "-12-31",
      dataTypes: cdoDatatypes[this.userParams.selectedElem],
      includeAttributes: true,
      format: "json"
    };
    for (let i in data_specs) {
      if (data_specs.hasOwnProperty(i)) {
        cdourl += "&" + i + "=" + data_specs[i]
      }
    }
    return cdourl
  }

  buildAcisParams = () => {
    const minors = {
      /* GHCN, CF6, DSM, Shef, TD3210, TD3200, TD3206, NWS, CRB, TD3205, NRCC, AWDN, SRCC */
      "maxt": [2, 13, 9, 4, 14, 1, 10, 16, 18, 15, 8, 6, 11],
      "mint": [2, 13, 9, 4, 14, 1, 10, 16, 18, 15, 8, 6, 11],
      "obst": [3, 99, 99, 4, 99, 1, 2, 99, 99, 99, 99, 99, 11],
      "pcpn": [2, 12, 8, 4, 13, 1, 9, 15, 17, 14, 7, 6, 10],
      "snow": [2, 11, 8, 4, 12, 1, 9, 14, 16, 13, 7, 6, 10],
      "snwd": [2, 10, 7, 4, 11, 1, 8, 13, 99, 12, 6, 99, 9]
    }
    const params = {
      sid: this.userParams.sid || "USC00304174",
      sdate: this.userParams.syr + "-01-01",
      edate: this.userParams.eyr + "-12-31",
      elems: [{
        name: this.userParams.selectedElem,
        vN: 0,
        interval: "dly",
        duration: "dly",
        add: "f,n,t"
      }],
      meta: ""
    }
    minors[this.userParams.selectedElem].map(vn => (
      params.elems.push({
        name: this.userParams.selectedElem,
        vN: vn,
        interval: "dly",
        duration: "dly"
      })
    ))
    return params 
  }
  
  updateShowResults = (value) => {
    this.setState({showResults: value, showAbout: !value})
  }


  render() {
    return (
      <div style={{padding:"0 1em", fontSize:"75%", fontFamily:"'Open Sans', sans-serif"}}>
        <h3>Climate Data Comparison Tool</h3>
        <UserInput userParams={this.userParams} updateShowResults={this.updateShowResults} />
        {this.state.showResults && <Results />}
        {this.state.showAbout && <About />}
      </div>
    )
  }
}

export default App



