import React, { Component } from 'react'
import { observer, inject } from "mobx-react"
import { observable } from "mobx"
import axios from "axios";
import './UserInput.css'
import InputRadio from "./InputRadio"
import InputText from "./InputText"
import StationName from "./StationName"
import StationSearch from "./StationSearch"
import helpIcon from "./icon-help.svg"

const fieldInputs = {
  sidInfo: {label: 'Station GHCN id (e.g. USC00304174):', placeholder: 'click ? for help'},
  yearInfo: {syr: 'Starting year:', eyr: 'Ending year:'},
  elemInfo: {maxt: 'Max temp', mint: 'Min temp', pcpn: 'Precip', snow: 'Snow', snwd: 'Snow depth', obst: 'Obs temp'}
}

@inject('store') @observer class UserInput extends Component {
  @observable srchParams = {
    anyId: "",
    srchName: "",
    stationList: []
  }

  constructor(props) {
    super(props)
    this.state = {
      showSearch: false,
      disabledElems: []
    } 
  }
  
  onChange = (event) => {
    if (event) {
      this.props.userParams[event.target.name] = event.target.value;
    }
  }
  
  onSidChange = (event) => {
    this.onChange(event);
    this.hideStationSearch();
    this.getMetaData();
    this.srchParams.stationList = [];
  }

  showStationSearch = () => {
    this.setState({ showSearch: true });
    fieldInputs.sidInfo.placeholder = '';
  }
  
  hideStationSearch = () => {
    this.setState({ showSearch: false });
  }

  getMetaData = () => {
    if (this.props.userParams.sid.length === 11) {
      axios
        .get("http://data.rcc-acis.org/StnMeta", { params: {
          params: {"sids":this.props.userParams.sid,
          elems: Object.keys(fieldInputs.elemInfo).join(),
          meta: "name,state,valid_daterange"
        }} })
        .then(res => {
          const meta = res.data.meta;
          let disabledElems = [];
          Object.keys(fieldInputs.elemInfo).forEach ((elem, cnt) => {
            if (meta[0].valid_daterange[cnt].length) {
              if (elem === this.props.userParams.selectedElem) {
                const vdr = meta[0].valid_daterange[cnt][0].substr(0,4) + " - " + meta[0].valid_daterange[cnt][1].substr(0,4);
                this.props.store.app.stnName = meta.length > 0 ? meta[0].name + ", " + meta[0].state + ' ' + vdr: "Unknown GHCN id";
              }
            } else {
              disabledElems.push(elem);
            }
          });
          this.setState({disabledElems: disabledElems});
        })
        .catch(err => {
          this.props.store.app.stnName = "";
        });
    } else {
      this.props.store.app.stnName = "";
    }
  }

  formatCdo = (results) => {
    const cdoDatatypes = {"maxt": "TMAX", "mint": "TMIN", "pcpn": "PRCP", "snow": "SNOW", "snwd": "SNWD", "obst": "TOBS"},
          elem = cdoDatatypes[this.props.userParams.selectedElem],
          elemattr = elem + "_ATTRIBUTES"
    var expectcrnt,
        newdays = {},
        addDay = (date, data) => {
          var value=null, attrs=[], cdoNote=null;
          date = date.substr(0, 10);
          if (data.hasOwnProperty(elem)) {
            value = data[elem];
            attrs = data[elemattr].split(",");
            cdoNote = "";
          } else if (data.hasOwnProperty("MDPR") || data.hasOwnProperty("MDSF")) {
            const mdelem = data.hasOwnProperty("MDPR") ? "MDPR" : "MDSF";
            const daelem = data.hasOwnProperty("MDPR") ? "DAPR" : "DASF";
            const mdelemattr = mdelem + "_ATTRIBUTES";
            value = data[mdelem];
            attrs = data[mdelemattr].split(",");
            cdoNote = "MD" + (data.hasOwnProperty(daelem) ? data[daelem] : "?");
            if (data.hasOwnProperty(daelem)) {
              this.props.store.app.cdoNotes = 'table-cell';
            }
          }
          if (value) {
            newdays[date] = {
              cdoValue: value, 
              cdoMflag: attrs[0], 
              cdoQflag: attrs[1], 
              cdoSflag: attrs[2], 
              cdoOtime: attrs.length === 4 ? attrs[3] : "-",
              cdoNote: cdoNote
            };
          }
          expectcrnt.setDate(expectcrnt.getDate() + 1);
        };
    if (results.length > 0) {
      expectcrnt = new Date(results[0]["DATE"].substr(0,4), 0, 1);
      const missrec = {[elem]: "-", [elem+"_ATTRIBUTES"]: ",,,"};
      const lastDay = new Date(this.props.userParams.eyr, 11, 31);
      for (let i = 0; i < results.length; i += 1) {
        const rec = results[i];
        const ymd = rec["DATE"].substr(0,10).split("-");
        const crntday = new Date(ymd[0], ymd[1] - 1, ymd[2]);
        if (crntday.getMonth() === 0 && crntday.getDate() === 1) {
          this.props.store.app.cdoStatus = "... Processing " + ymd[0];
        }
        while (expectcrnt < crntday) {
          addDay(expectcrnt.toISOString(), missrec);
        } 
        addDay(rec["DATE"], rec);
      }
      while (expectcrnt <= lastDay) {
        addDay(expectcrnt.toISOString(), missrec);
      }
      Object.assign(this.props.store.app.cdoData, newdays);
      this.props.store.app.cdoStatus = "Results"
    } else {
      this.props.store.app.cdoStatus = "- no data available";
    }
  }
  
//  getCdoAnother = () => {
//    this.props.userParams.cdoProcessYear += 1;
//    if (this.props.userParams.cdoProcessYear <= parseInt(this.props.userParams.eyr, 10)) {
//      this.getCdoData();
//    } else if (this.inprogress <= 0 && this.props.store.app.cdoStatus.indexOf("rror") === -1) {
//      this.props.store.app.cdoStatus = null;
//    }
 // }
  
  getCdoData = () => {
    axios
      .get(this.props.userParams.cdoUrl)
      .then(res => {
        this.formatCdo(res.data);
      })
      .catch(err => {
        console.log("CDO Request Error: " + err);
        this.props.store.app.cdoStatus = "* Error *";
      });
  }
  
  formatAcis = (res) => {
    if (res.data.error) {
      this.props.store.app.acisStatus = "Error - " + res.data.error;    
    } else {
      const newdays = {};
      for (let i = 0; i < res.data.data.length; i += 1) {
        const results = res.data.data[i];
        newdays[results[0]] = {
          acisValue: results[1][0],
          acisFlag: results[1][1], 
          acisSource: results[1][2],
          acisOtime: results[1][3],
          acisOther: []
        };
        for (let j = 2; j <= 14; j += 1) {
          if (results[j] !== "M") {
            this.props.store.app.acisHas[j - 2] = 'table-cell';
            newdays[results[0]].acisOther.push(results[j]);
          } else {
            newdays[results[0]].acisOther.push("-");
          }
        }
      }
      Object.assign(this.props.store.app.acisData, newdays);
      this.props.store.app.acisStatus = null;
    }
  }
  
  getAcisData = () => {
   axios
    .get("http://data.rcc-acis.org/StnData", { params: {params: this.props.userParams.acisParams} })
    .then(res => {
      this.formatAcis(res);
    })
    .catch(err => {
      console.log("ACIS Request Error: " + (err.response.data || err.response.statusText));
      this.props.store.app.acisStatus = err;
    });
  }
  
  resetStore = () => {
    this.props.store.app.cdoData = {};
    this.props.store.app.cdoNotes = 'none';
    this.props.store.app.cdoStatus = "... Obtaining data";
    this.props.store.app.acisStatus = "... Processing";
    this.props.store.app.acisData = {};
    this.props.store.app.acisHas = this.props.store.app.acisHas.map(() => 'none');
  }

  getData = (e) => {
    e.preventDefault();
    this.hideStationSearch();
    this.resetStore();
    this.props.updateShowResults(true);
    if (this.props.userParams.acisParams) {
       this.getAcisData();
    } else {
      this.props.store.app.acisStatus = "- No data from ACIS";
    }
    this.getCdoData();
  }
  
  resetInput = (e) => {
    this.resetStore();
    this.hideStationSearch();
    this.props.userParams.sid = "";
    this.props.userParams.syr = (new Date()).getFullYear();
    this.props.userParams.eyr = (new Date()).getFullYear();
    this.props.userParams.selectedElem = "pcpn";
    this.props.store.app.stnName = "";
    this.setState({disabledElems: []});
    this.srchParams.stationLIst = [];
  }

  render() {
    return (
      <div>
        <InputText name="sid" size="13" value={this.props.userParams.sid} placeholder={fieldInputs.sidInfo.placeholder} label={fieldInputs.sidInfo.label} onChange={this.onSidChange} />
        <img
          src={helpIcon}
          alt="help" 
          style={{marginLeft:'3px', verticalAlign:"bottom"}} 
          onClick={this.showStationSearch}
        />
        <StationName /><br />

        {this.state.showSearch && 
          <StationSearch userParams={this.props.userParams} srchParams={this.srchParams} hideStationSearch={this.hideStationSearch} onSidChange={this.onSidChange} />
        }
          
        <div className="yearInput">
          {Object.keys(fieldInputs.yearInfo).map(ky => (
            <InputText key={ky} name={ky} size="5" value={this.props.userParams[ky]} label={fieldInputs.yearInfo[ky]} onChange={this.onChange} />
          ))}
        </div>

        <fieldset className="varboxes">
          <legend>Variable:</legend>
          <InputRadio name="selectedElem" values={fieldInputs.elemInfo} disabled={this.state.disabledElems} selected={this.props.userParams.selectedElem} onChange={this.onSidChange} />
        </fieldset>

        <br />
        <button onClick={this.getData}>Go</button>
        <button onClick={this.resetInput}>Reset</button>
      </div>
    )
  }
}

export default UserInput



